.App {
  max-width: 1000px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0px auto;
}

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.App-header button {
  border: none;
  color: white;
  cursor: pointer;
  background-color: #346;
  border-radius: 3px;
  padding: 5px 20px;
  font-size: 1em;
  line-height: 23px;
}

.Game-Container {
  position: relative;
}

.Board {
  display: flex;
  flex-direction: row;
  border: 5px solid white;
  border-radius: 10px;
  flex-wrap: wrap;
  width: min(85vw, 75vh, 700px);
  height: min(85vw, 75vh, 700px);
  margin: 20px auto;
  user-select: none;
}

.Board-Element {
  border-right: 1px solid white;
  border-bottom: 1px solid white;
  display: flex;
  width: calc((100% - 7px) / 4);
  height: calc((100% - 7px) / 4);
  align-items: center;
  justify-content: center;
  font-size: 5em;
  cursor: pointer;
}

.Board-Element.disabled {
  background-color: #333;
  /* cursor: not-allowed; */
}

.Board-Element.selected {
  background-color: #3a3;
  color: #000;
}
.Board-Element.highlight {
  background-color: #387;
}

.Board-Element:nth-child(2n) {
  border-right-width: 5px;
}

.Board-Element:nth-child(4n) {
  border-right: none;
}

.Board-Element:nth-child(n+5):nth-child(-n+8) {
  border-bottom-width: 5px;
}

/* .Board-Element:nth-child(n+46):nth-child(-n+54) {
  border-bottom-width: 5px;
} */

.Board-Element:nth-child(n+13) {
  border-bottom: none;
}

.Board-Element.color1 {
  color: #0ff;
}
.Board-Element.color2 {
  color: #99f;
}
.Board-Element.color3 {
  color: #ff0;
}
.Board-Element.color4 {
  color: #09f;
}
/*
.Board-Element.color5 {
  color: #f90;
}
.Board-Element.color6 {
  color: #9f0;
}
.Board-Element.color7 {
  color: #94f;
}
.Board-Element.color8 {
  color: #0f9;
}
.Board-Element.color9 {
  color: #f09;
} */
.Board-Element.error {
  color: #f00;
}
.Actions {
  width: min(85vw, 75vh, 700px);
  margin: 20px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.Actions button {
  align-content: center;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  border: none;
  color: black;
  cursor: pointer;
  display: flex;
  font-size: 1.5em;
  justify-content: center;
  line-height: 1.5em;
  margin: 2% 10% 0 0;
  width: 10%;
}

.Actions button:nth-child(5n) {
  margin-right: 0;
}

.About {
  margin: 0px auto;
  width: 75%;
}

.Game-Finished {
  animation: fadein 2s;
  background: rgba(50, 150, 50, 0.95);
  height: 100%;
  position: absolute;
  width: 100%;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.Game-Finished h1 {
  font-size: 5em;
  margin: 20% auto;
}
